.section-footer1 {
  background-image: url(../images/bg-footer.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  h5 {
    color: $white;
    font-weight: bold;
  }
  p {
    font-size: 0.8rem;
    color: $white;
    font-weight: 200;
  }
  ul {
    padding-left: 0;
  }
  li {
    list-style: none;
    margin-bottom: .3rem;
    .items-menu {
      font-size: 0.8rem;
      color: $white;
      font-weight: 200;
      text-decoration: none;
      &:hover {
        color: $gray;
      }
    }
  }
  small {
    color: $white;
  }
}
.content-footer {
  @media (min-width: 1200px) {
    width: 90%;
  }
}
.section-footer2 {
  padding: .5rem 0;
  img {
    width: 40px;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
  }
}
.section-footer3 {
  padding: .2rem 0;
  background-color: #4f4f4f;
  color: #bdbdbd;
  small {
    font-size: 12px;
  }
  a {
    color: #bdbdbd;
    text-decoration: none;
    transition: 0.3s;
    &:hover {
      color: $gray-light;
    }
  }
}